/* React */
#root {
  width: 100%;
}

button {
  border-color: #f3f8f8;
  background-color: #fff;
}

.container {
  display: flex;
  justify-content: space-around;
}
.translate_lang,
.translated_lang {
  width: 45%;
}
/*드롭다운 css*/
.dropdown_text {
  display: block;
  margin: 0 auto;
  padding: 10px;
  background: #fff;
  border-radius: 7px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 1px rgba(50, 50, 50, 0.1);
  cursor: pointer;
  outline: none;
  font-weight: bold;
  color: #8aa8bd;
}
.dropdown_text p {
  display: -webkit-inline-box;
}
.dropdown_lang {
  position: absolute;
  width: 45%;
}
.dropdown_lang:after {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  right: 15px;
  top: 16px;
  border-width: 6px 6px 0 6px;
  border-style: solid;
  border-color: #8aa8bd transparent;
}

.dropdown_lang .dropdown_lang_select {
  margin-top: 15px;
  position: relative;
  display: none;
  background: white;
  border-radius: inherit;
  border: 1px solid rgba(0, 0, 0, 0.17);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  font-weight: normal;
  cursor: pointer;
}
.dropdown_lang .dropdown_lang_select:after {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  bottom: 100%;
  right: 15px;
  border-width: 0 6px 6px 6px;
  border-style: solid;
  border-color: #fff transparent;
}
.dropdown_lang .dropdown_lang_select:before {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  bottom: 100%;
  right: 13px;
  border-width: 0 8px 8px 8px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.1) transparent;
}

.dropdown_lang:hover .dropdown_lang_select {
  display: block;
}
.dropdown_lang_select li {
  cursor: pointer;
}
.dropdown_lang .dropdown_lang_select li p {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #8aa8bd;
  border-bottom: 1px solid #e6e8ea;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 1);
  transition: all 0.3s ease-out;
  cursor: pointer;
}

.wrapper-dropdown-3 .dropdown li p i {
  float: right;
  color: inherit;
}

.dropdown_lang .dropdown_lang_select li:first-child {
  border-radius: 7px 7px 0 0;
}

.dropdown_lang .dropdown_lang_select li:last-child {
  border: none;
  border-radius: 0 0 7px 7px;
}

.dropdown_lang .dropdown_lang_select li p:hover {
  background: #f3f8f8;
}

/* 번역 구역 css */

.translate_textarea,
.translated_textarea {
  margin-top: 50px;
  resize: none;
  width: -webkit-fill-available;
  height: 200px;
  padding: 10px;
  background: #fff;
  border-radius: 7px 7px 0 0;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 1px rgba(50, 50, 50, 0.1);
  outline: none;
  font-weight: bold;
  color: black;
}

/* 메뉴 버튼 css */

.menu_button {
  display: flex;
  padding: 1px;
  justify-content: flex-end;
  border-radius: 0 0 7px 7px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-top: 0;
  box-shadow: 0 1px 1px rgba(50, 50, 50, 0.1);
}
.menu_button button {
  border: 0;
  background: none;
  width: 50px;
}
.menu_button button:active {
  transform: translateY(2px);
}
.button_img {
  width: 100%;
  height: 100%;
  min-width: 10px;
}

/* 반응형 css */
.responsive {
  display: none;
}
@media only screen and (max-width: 960px) {
  .responsive {
    display: block;
  }
  .default {
    display: none;
  }
  .translate_lang {
    width: 90%;
  }
  .dropdown_position_responsive {
    position: absolute;
    display: flex;
    width: 90%;
    justify-content: space-between;
  }
  .dropdown_lang {
    position: relative;
  }
  .translate_textarea {
    height: 150px;
    margin-top: 70px;
  }
  .translated_textarea {
    margin-top: 13px;
    height: 150px;
  }
  .menu_button {
    display: flex;
  }
}

/* 헤더 css */
#header {
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  width: 100%;
  height: 100px;
  margin-bottom: 30px;
  border-bottom: #000 2px solid;
}

#header img {
  width: 15%;
}
#header p {
  display: flex;
  align-items: center;
}

